a.active > button {
  background-color: #2196f321 !important;
  color: #2196f3;
}
a > button.MuiButtonBase-root.MuiButton-root.MuiButton-text {
  padding: 6px 16px;
  border-radius: 2em;
}
@media (max-width: 959.95px) {
  header svg.MuiSvgIcon-root {
    font-size: 30px;
    height: 30px;
    width: 30px;
  }
  a.active > button {
    background-color: transparent !important;
  }
  header button.MuiButtonBase-root.MuiButton-root.MuiButton-text {
    padding: 0 !important;
    min-width: auto;
  }
  .MuiButton-startIcon {
    margin: 0;
  }
  header .MuiGrid-spacing-xs-1 > .MuiGrid-item {
    padding: 0;
  }
  header .MuiGrid-container {
    justify-content: space-between;
  }
  header span.MuiIconButton-label > img {
    width: 25px !important;
  }
}
